import { O, P, Q, T, V } from "./define.js";

// -----------------------------------------------------------------------------
// åˆ¶å¾¡é…åˆ—ã‚’é€†é †ã«æ¤œç´¢ã—ã¦ã€åˆ¶å¾¡ã‚­ãƒ¼ã‹ã‚‰è¨­å®šå€¤ã‚’å–å¾—ã™ã‚‹
// -----------------------------------------------------------------------------
// `t`:ãƒ†ãƒ³ãƒ         1 ~ 480
// `v`:éŸ³é‡           0 ~ 15
// `!`:ä¸€æ™‚çš„ãªéŸ³é‡   0 ~ 15
// `p`:ãƒ‘ãƒ³          -8 ~ 0 ~ +8ã€å·¦ã€œä¸­å¤®ã€œå³
// `o`:ã‚ªã‚¯ã‚¿ãƒ¼ãƒ–     1 ~ 9
// `q`:ã‚²ãƒ¼ãƒˆã‚¿ã‚¤ãƒ    1 ~ 8ã€ã‚¹ã‚¿ãƒƒã‚«ãƒ¼ãƒˆçš„ãªåŠ¹æœ
// `l`:éŸ³é•·
// `&`:ã‚¿ã‚¤ or ã‚¹ãƒ©ãƒ¼
const searchCTRL = (ary, chr, def) => {
  let rtn = { key: chr, value: def, index: -1 };
  for (var i = ary.length - 1; i >= 0; --i) {
    if (ary[i].ctrl !== chr) continue;
    rtn.value = Number(ary[i].param);
    rtn.index = i;
    break;
  }
  return rtn;
};

// -----------------------------------------------------------------------------
// åˆ¶å¾¡é…åˆ—ã‚’é€†é †ã«æ¤œç´¢ã—ã¦ã€åˆ¶å¾¡ã‚­ãƒ¼ã‹ã‚‰è¨­å®šå€¤ã‚’å–å¾—ã™ã‚‹
// -----------------------------------------------------------------------------
// `m`:LFO
const searchLFO = (ary, chr) => {
  let rtn = { key: chr, value: ``, index: -1 };
  for (var i = ary.length - 1; i >= 0; --i) {
    if (ary[i].ctrl !== chr) continue;
    rtn.value = ary[i].param;
    rtn.index = i;
    break;
  }
  return rtn;
};

// -----------------------------------------------------------------------------
// åˆ¶å¾¡é…åˆ—ã¨éŸ³éšãƒ‡ãƒ¼ã‚¿ã‹ã‚‰ã€Web Audio APIã§åˆ©ç”¨ã—ã‚„ã™ã„å½¢å¼ã¸å¤‰æ›
const convert = (ary, tn) => {
  const rtn = {
    t: Math.max(Math.min(searchCTRL(ary, `t`, T.def).value, T.max), T.min),
    v: Math.max(Math.min(searchCTRL(ary, `v`, V.def).value, V.max), V.min),
    p: Math.max(Math.min(searchCTRL(ary, `p`, P.def).value, P.max), P.min),
    o: Math.max(Math.min(searchCTRL(ary, `o`, O.def).value, O.max), O.min),
    q: Math.max(Math.min(searchCTRL(ary, `q`, Q.def).value, Q.max), Q.min),
    tn: `c`,
    l: `4`,
    j: 0,
    m: ``,
  };

  // ---------------------------------------------------------------------------
  // éŸ³éšæŒ‡å®šã®å‰ã«ä¸€æ™‚çš„ãªéŸ³é‡æŒ‡å®šãŒã‚ã£ãŸå ´åˆ
  // åˆ¤å®šå¾Œã€ä¸€æ™‚çš„ãªæŒ‡å®šéŸ³é‡ã¸å¤‰æ›´ã—ã€åˆ¶å¾¡é…åˆ—ã‹ã‚‰å–ã‚Šé™¤ã
  let v = searchCTRL(ary, `!`, V.def);
  if (v.index !== -1) {
    rtn.v = Math.max(Math.min(v.value, V.max), V.min);
    ary.splice(v.index, 1);
  }

  // ---------------------------------------------------------------------------
  // éŸ³éšæŒ‡å®šã®å‰ã«ã‚¿ã‚¤orã‚¹ãƒ©ãƒ¼æŒ‡å®šãŒã‚ã£ãŸå ´åˆ
  // åˆ¤å®šå¾Œã€jointãƒ•ãƒ©ã‚°ã‚’ç«‹ã¦ã¦ã€åˆ¶å¾¡é…åˆ—ã‹ã‚‰å–ã‚Šé™¤ã
  let j = searchCTRL(ary, `&`, rtn.j);
  rtn.j = Math.max(Math.min(j.value, 1), 0);
  if (rtn.j === 1) {
    ary.splice(j.index, 1);
  }

  // ---------------------------------------------------------------------------
  // LFO
  // åˆ¤å®šå¾Œã€åˆ¶å¾¡é…åˆ—ã‹ã‚‰å–ã‚Šé™¤ã
  let m = searchLFO(ary, `m`);
  if (m.index !== -1) {
    rtn.m = m.value;
    ary.splice(m.index, 1);
  }

  // ---------------------------------------------------------------------------
  // éŸ³é•·
  let l = searchCTRL(ary, `l`, rtn.l);
  rtn.l = tn.len === `` ? l.value.toString() : tn.len === `.` ? l.value.toString() + `.` : tn.len;

  // ---------------------------------------------------------------------------
  // éŸ³éš
  rtn.tn = (tn.tone + `  `).slice(0, 2);
  return rtn;
};

// -----------------------------------------------------------------------------
// MMLãƒ‘ãƒ¼ã‚µãƒ¼
const parser = (mml_part) => {
  const parser_result = [];
  mml_part.forEach((mml) => {
    let tmp = { ctrl:``, param:``, tone:``, len:``, };
    let dmy = JSON.parse(JSON.stringify(tmp));
    let ctrlAry = [];
    let toneAry = [];

    // -------------------------------------------------------------------------
    // ã²ã¨æ–‡å­—ãšã¤èª­ã¿è¾¼ã‚“ã§ã€
    // åˆ¶å¾¡é…åˆ—ï¼ˆctrlAryï¼‰ or éŸ³éšé…åˆ—ï¼ˆtoneAryï¼‰ã¸ç©ã¿è¾¼ã‚€
    Array.prototype.forEach.call(mml, (item) => {
      const chr = item.toLowerCase();
      switch (chr) {
        case `t`:
        case `v`:
        case `p`:
        case `o`:
        case `l`:
        case `q`:
        case `>`:
        case `<`:
        case `!`:
        case `m`:
          if (dmy.ctrl !== chr || dmy.tone !== chr) {
            if (dmy.ctrl !== ``) ctrlAry.push(dmy);
            if (dmy.tone !== ``) toneAry.push(convert(ctrlAry, dmy));
            dmy = JSON.parse(JSON.stringify(tmp));
          }
          switch (chr) {
            case `t`: // ãƒ†ãƒ³ãƒ
              dmy.ctrl = `t`;
              break;
            case `v`: // éŸ³é‡
              dmy.ctrl = `v`;
              break;
            case `p`: // ãƒ‘ãƒ³
              dmy.ctrl = `p`;
              break;
            case `o`: // éŸ³éš
              dmy.ctrl = `o`;
              break;
            case `q`: // ã‚²ãƒ¼ãƒˆã‚¿ã‚¤ãƒ 
              dmy.ctrl = `q`;
              break;
            case `l`: // éŸ³é•·
              dmy.ctrl = `l`;
              break;
            case `>`: // ã‚ªã‚¯ã‚¿ãƒ¼ãƒ–ä¸Šã’
              {
                let o = searchCTRL(ctrlAry, `o`, 4);
                dmy.ctrl = o.key;
                dmy.param = (o.value + 1).toString();
              }
              break;
            case `<`: // ã‚ªã‚¯ã‚¿ãƒ¼ãƒ–ä¸‹ã’
              {
                let o = searchCTRL(ctrlAry, `o`, 4);
                dmy.ctrl = o.key;
                dmy.param = (o.value - 1).toString();
              }
              break;
            case `!`: // ä¸€æ™‚çš„ãªéŸ³é‡
              dmy.ctrl = `!`;
              break;
            case `m`: // LFO
              dmy.ctrl = `m`;
              break;
          }
          break;

        case `&`: // ã‚¿ã‚¤ or ã‚¹ãƒ©ãƒ¼
          if (dmy.ctrl !== chr || dmy.tone !== chr) {
            if (dmy.ctrl !== ``) ctrlAry.push(dmy);
            if (dmy.tone !== ``) toneAry.push(convert(ctrlAry, dmy));
            dmy = JSON.parse(JSON.stringify(tmp));
            dmy.ctrl = `&`;
            dmy.param = `1`;
          }
          break;

        case `r`: // ä¼‘ç¬¦
        case `a`: // éŸ³éš ãƒ©
        case `b`: // éŸ³éš ã‚·
        case `c`: // éŸ³éš ãƒ‰
        case `d`: // éŸ³éš ãƒ¬
        case `e`: // éŸ³éš ãƒŸ
        case `f`: // éŸ³éš ãƒ•ã‚¡
        case `g`: // éŸ³éš ã‚½
          if (dmy.ctrl !== chr || dmy.tone !== chr) {
            if (dmy.ctrl !== ``) ctrlAry.push(dmy);
            if (dmy.tone !== ``) toneAry.push(convert(ctrlAry, dmy));
            dmy = JSON.parse(JSON.stringify(tmp));
          }
          dmy.tone += chr;
          break;

        case `-`: // åŠéŸ³ã‚µã‚² or è² ãƒ‘ãƒ©ãƒ¡ãƒ¼ã‚¿
        case `+`: // åŠéŸ³ã‚¢ã‚² or æ­£ãƒ‘ãƒ©ãƒ¡ãƒ¼ã‚¿
        case `#`: // åŠéŸ³ã‚¢ã‚² â† éŸ³éšã®å¾Œã®è¨˜è¿°ã®ã¿æœ‰åŠ¹
          if (dmy.tone.length === 1 && `a` <= dmy.tone && dmy.tone <= `g`) {
            dmy.tone += chr;
          }
          if (dmy.ctrl === `p` && (chr === `-` || chr === `+`)) {
            dmy.param += chr;
          }
          break;

        case `.`: // ä»˜ç‚¹â†é€£ç¶šã—ãŸä»˜ç‚¹ï¼ˆäºŒé‡ã€ä¸‰é‡ï¼‰ã¯ã€ï¼’å€‹ç›®ä»¥é™ã¯ç„¡è¦–
                  // LFOãƒ‘ãƒ©ãƒ¡ãƒ¼ã‚¿ï½œå°æ•°å€¤
          if (dmy.ctrl === `m`) dmy.param += chr;
          if (dmy.tone !== `` && !dmy.len.includes(`.`)) {
            dmy.len += chr;
          }
          break;

        case `/`: // LFOãƒ‘ãƒ©ãƒ¡ãƒ¼ã‚¿ï½œåŒºåˆ‡ã‚Šæ–‡å­—
          if (dmy.ctrl === `m`) dmy.param += chr;
          break;

        case `0`:
        case `1`:
        case `2`:
        case `3`:
        case `4`:
        case `5`:
        case `6`:
        case `7`:
        case `8`:
        case `9`:
          if (dmy.ctrl !== ``) dmy.param += chr;
          if (dmy.tone !== ``) dmy.len += chr;
          break;

        default: // ç„¡è¦–
      }
    });

    // -------------------------------------------------------------------------
    // æœ€å¾Œã®è¦ç´ ã‚’ç©ã¿è¾¼ã‚“ã§ã„ãªã„ã®ã§ã€
    // åˆ¶å¾¡é…åˆ—ï¼ˆctrlAryï¼‰ or éŸ³éšé…åˆ—ï¼ˆtoneAryï¼‰ã¸ç©ã¿è¾¼ã‚€
    if (dmy.ctrl !== ``) ctrlAry.push(dmy);
    if (dmy.tone !== ``) toneAry.push(convert(ctrlAry, dmy));

    // -------------------------------------------------------------------------
    // ï¼‘éŸ³ãšã¤ã®éŸ³éšé…åˆ—ã‹ã‚‰ã€Œï¼†ã€ã«ã‚ˆã‚‹éŸ³ã®çµåˆã‚’è€ƒæ…®ã—ã¦ã€
    // éŸ³é‡ã€ãƒ‘ãƒ³ãƒãƒƒãƒˆã€ã‚ªã‚¯ã‚¿ãƒ¼ãƒ–ã€éŸ³é•·ã€éŸ³éšã‚’é…åˆ—åŒ–ã™ã‚‹
    // ãƒ†ãƒ³ãƒã¨ã‚²ãƒ¼ãƒˆã‚¿ã‚¤ãƒ ã¯ã€çµåˆå‰ã®æŒ‡å®šå€¤ã‚’æ¡ç”¨ã™ã‚‹
    let tmpAry = [];
    for (let loop = 0; loop < toneAry.length; ++loop) {
      let rtn = {
        t: toneAry[loop].t,
        q: toneAry[loop].q,
        v: [],
        p: [],
        o: [],
        l: [],
        m: [],
        tn: [],
      };
      if (toneAry[loop].j === 1) {
        // ï¼†è¨˜å·ã‚ã‚Šã€å‰è¦ç´ ã‹ã‚‰ã®å»¶é•·
        if (0 < tmpAry.length) {
          rtn = tmpAry[tmpAry.length - 1];
          rtn.v.push(toneAry[loop].v);
          rtn.p.push(toneAry[loop].p);
          rtn.o.push(toneAry[loop].o);
          rtn.l.push(toneAry[loop].l);
          rtn.m.push(toneAry[loop].m);
          rtn.tn.push(toneAry[loop].tn);
        } else {
          rtn.v.push(toneAry[loop].v);
          rtn.p.push(toneAry[loop].p);
          rtn.o.push(toneAry[loop].o);
          rtn.l.push(toneAry[loop].l);
          rtn.m.push(toneAry[loop].m);
          rtn.tn.push(toneAry[loop].tn);
          tmpAry.push(rtn);
        }
      } else {
        // ï¼†è¨˜å·ãªã—ã€æ–°è¦ç´ ã¨ã—ã¦è¿½åŠ 
        rtn.v.push(toneAry[loop].v);
        rtn.p.push(toneAry[loop].p);
        rtn.o.push(toneAry[loop].o);
        rtn.l.push(toneAry[loop].l);
        rtn.m.push(toneAry[loop].m);
        rtn.tn.push(toneAry[loop].tn);
        tmpAry.push(rtn);
      }
    }

    // -------------------------------------------------------------------------
    // ãƒ‡ãƒãƒƒã‚°ãƒ­ã‚°
    tmpAry.forEach((x) => {
      console.log(`T${("   " + x.t.toString()).slice(-3)} Q[${x.q}] V[${x.v}] P[${x.p}] O[${x.o}][${x.tn}] L[${x.l}] LFO[${x.m}]`);
    });
    console.log("-".repeat(40));

    // -------------------------------------------------------------------------
    // ãƒ‘ãƒ¼ã‚¹çµæœã‚’ãƒ‘ãƒ¼ãƒˆé…åˆ—ã¸
    let part = {};
    part.toneAry = tmpAry;
    parser_result.push(part);
  });
  return parser_result;
};

export { parser };
